import { Button, TextField, Typography } from '@mui/material'
import { Stack } from '@mui/system'
import { useEffect, useState } from 'react'
import { useNavigate } from 'react-router'
import { useAppSelector, useAuth } from 'redux/hooks'

export default function VerifyToken() {
	const loginInfo = useAppSelector((s) => s.UI.loginInfo)
	const [token, setToken] = useState('')
	const nav = useNavigate()
	const { verifyToken } = useAuth()

	useEffect(() => {
		if (!loginInfo || !loginInfo.otpSecret) {
			nav('/login')
		}
	}, [loginInfo, nav])

	if (!loginInfo) {
		return null
	}
	const handleSubmit = async () => {
		try {
			const { email, password, otpSecret } = loginInfo
			await verifyToken(email, password, `${otpSecret}`, token)
		} catch (error) {}
	}
	return (
		<Stack justifyContent="center" alignItems="center">
			<Stack
				gap={1}
				component="form"
				noValidate
				autoComplete="off"
				onSubmit={(e) => {
					e.preventDefault()
					e.stopPropagation()
					handleSubmit()
				}}
			>
				<Typography>アプリに登録したうえで、下のコード入力をしてください</Typography>
				<TextField
					variant="outlined"
					required
					margin="dense"
					fullWidth
					size="small"
					label={'コード'}
					autoFocus
					autoComplete="on"
					placeholder="6数字を入力してください"
					value={token}
					onChange={(e) => {
						setToken(e.target.value)
					}}
				/>
				<Button variant="outlined" type="submit">
					コード認証
				</Button>
			</Stack>
		</Stack>
	)
}
